/* Scrolling control */

.snap-container {
  height: 100vh;
  overflow: auto;
  scroll-snap-type: y mandatory;
}

.snap-container > div {
  scroll-snap-align: start;
}

.snap-hole {
  height: 101vh;
  width: 100vw;
  overflow: hidden;
}

.logo {
  width: 4.5rem;
  max-height: 100%;
